.slider {
    flex-direction: column;
}

.slider h2{
  font-weight: bold;
}

.slider .owl-nav button {
  display: block;
  position: absolute;
  top: 50%;
  background-color: #000 !important;
  color: #fff !important;
  font-size: medium;
  margin: 0;
  transition: all 0.3s ease-in;
}

.slider .owl-nav button.owl-prev {
  left: -10px;
}

.slider .owl-nav button.owl-next {
  right: -10px;
}

.slider .owl-nav button.owl-prev:hover, .slider .owl-nav button.owl-next:hover{
    background-color: rgba(0,0,0,0.7) !important;
    transition: all 0.3s ease-in;
}

.slider .owl-dots {
  text-align: center;
  padding-top: 15px;
}
.slider .owl-dots button.owl-dot {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  display: inline-block;
  background: #ccc;
  margin: 0 3px;
}
.slider .owl-dots button.owl-dot.active {
  background-color: #000;
}
.slider .owl-dots button.owl-dot:focus {
  outline: none;
}
.slider .owl-nav button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: black !important;
}

.slider .owl-nav button:focus {
    outline: none;
}

.slider .owl-carousel .owl-nav button.owl-prev,.slider .owl-carousel .owl-nav button.owl-next,.slider .owl-carousel button.owl-dot{
    padding: 12px !important;
}