.box-div {
    background: linear-gradient(0deg, rgba(11, 11, 13, 0.5), rgba(11, 11, 13, 0.5)), url("../../assets/background/dp.jpg");
    background-attachment: fixed;
    background-position: left, right ;
    background-repeat: no-repeat;
    background-color: black;
    background-size: 100% 100%;
    border-radius: 20%;
    width: 100%;
    height: 100vh;
    width: 85%;
    margin:auto;
    
    
}
.box-div header{
    margin-left: 10%;
}
@media only screen and (max-width: 1000px) {
    .box-div{
        background-image: none;
        
    }
  }
.box-div .head-div{
    margin: 0 auto;
}
.box-div .div-1{
    margin: 0 auto;
}
.box-div .head {
    font-family: 'Rajdhani Medium', 'rajdhani' !important;
    color: white;
    position: absolute;
    top: 50%;
    left: 0px;
    right: 0px;
    z-index: 1;
    float: left;
    transform: translateY(-50%);
    
    line-height: 0.1vh;
    min-height: 100vh;
    display: flex;
}

.box-div .head-div-first-text {
   font-size: 60px;
   font-weight: normal;
   text-transform: uppercase;
   padding: 0!important;
}

.box-div .head-div-second-text {
   font-size: 40px;
   padding: 0 0 0 0px;
   font-weight: normal;
}

.box-div .head-div-third-text {
    font-family: 'Rajdhani Medium', 'rajdhani' !important;
    max-width: 500px;
    font-size: 17px;
    margin: 15px 0;
    letter-spacing: 10px;
    font-weight: 600;
    text-transform: uppercase;
    white-space: pre-wrap;
    padding-left: 15px;
}
.box-div .scroll-btn {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
}
.box-div .scroll-btn > *:hover, .box-div .scroll-btn > *:focus, .box-div .scroll-btn > *.active {
    color: #fff;
}
.box-div .scroll-btn > *:hover, .box-div .scroll-btn > *:focus, .box-div .scroll-btn > *:active, .box-div .scroll-btn > *.active {
    opacity: 0.8;
    filter: alpha(opacity=80);
}
.box-div .scroll-btn .mouse {
    position: relative;
    display: block;
    width: 35px;
    height: 55px;
    margin: 0 auto 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    border: 3px solid white;
    border-radius: 23px;
}
.box-div .scroll-btn .mouse > * {
    position: absolute;
    display: block;
    top: 29%;
    left: 50%;
    width: 8px;
    height: 8px;
    margin: -4px 0 0 -4px;
    background: white;
    border-radius: 50%;
    -webkit-animation: ani-mouse 2.5s linear infinite;
    -moz-animation: ani-mouse 2.5s linear infinite;
    animation: ani-mouse 2.5s linear infinite;
}

.box-div .scroll-btn.dark .mouse{
   border: 3px solid #212121 !important;
}

.box-div .scroll-btn.dark .mouse > * {
   background: #212121 !important;
}


/* mouse animation */

@-webkit-keyframes ani-mouse {
    0% {
        opacity: 1;
        top: 29%;
   }
    15% {
        opacity: 1;
        top: 50%;
   }
    50% {
        opacity: 0;
        top: 50%;
   }
    100% {
        opacity: 0;
        top: 29%;
   }
}
@-moz-keyframes ani-mouse {
    0% {
        opacity: 1;
        top: 29%;
   }
    15% {
        opacity: 1;
        top: 50%;
   }
    50% {
        opacity: 0;
        top: 50%;
   }
    100% {
        opacity: 0;
        top: 29%;
   }
}
@keyframes ani-mouse {
    0% {
        opacity: 1;
        top: 29%;
   }
    15% {
        opacity: 1;
        top: 50%;
   }
    50% {
        opacity: 0;
        top: 50%;
   }
    100% {
        opacity: 0;
        top: 29%;
   }
}
