.footer{
    margin-bottom: -30px;
    width: 100%;
    background-color: #f8f8f8 !important;
    display: flex;
    flex-wrap: wrap;
    padding: 40px 20px 5px 20px;
}

.footer .left {
    width: 50%;
    margin: 0px 60px 30px;
}

.footer .right {
    width: 35%;
    margin-left: 60px;
    margin-top: 0;
}


.footer .left h2, .footer .right h3{
    font-weight: bold;
}

.footer .connect-text {
    margin: 10px 0 0 !important;
}

.footer .social-links span a{
    color: black;
    font-size: 180%;
    padding: 0 4%;
    transition:all 0.5s ease;
}

.footer .social-links span .facebook:hover,.footer .social-links span .facebook:focus{
    color: #4267B2;
    transition:all 0.5s ease;
}

.footer .social-links span .instagram:hover,.footer .social-links span .instagram:focus{
    color: #cc2366; 
    transition:all 0.5s ease;
}

.footer .social-links span .linkedin:hover,.footer .social-links span .linkedin:focus{
    color: #0e76a8;
    transition:all 0.5s ease;
}

.footer h2 {
    margin: 10px 0px;
    font-weight: 500;
    line-height: 1.2;
}

.footer .lead {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.6;
    margin-bottom: 20px;
}

.footer .bottom-div{
    text-align: center;
}

.footer .bottom-div .first-text{
    font-weight: bold;
}

.footer .bottom-div .second-text{
    font-size: 14px;
}

.Github:hover{
    color: white;
    background-color: black;
    border-radius: 100%;
}
.Github{
    transition-duration: 1s;
    border: none;
}