.header-nav.transparent {
	 background: transparent !important;
	 transition: all 0.5s ease-in;
}

.header-nav .navbar-toggler {
	 border-color: #fff;
	 transition: all 0.5s ease;
}

.header-nav .nav-link {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 font-family: 'Rajdhani Medium', 'rajdhani' !important;	 
	 font-size: 16px;
	 font-weight: bold;
	 text-transform: uppercase;
	 transition: all 0.5s ease;
	 letter-spacing: 3px;
	 padding: 1rem 0;
}

.header-nav.transparent .nav-link{
	color: white !important;
}

.header-nav.brand-name{
	padding: 1.5rem 0;
	font-family: 'Rajdhani Medium', 'rajdhani' !important;	 
	 font-size: 16px;
	 font-weight: bold;
	 text-transform: uppercase;
	 transition: all 0.5s ease;
	 letter-spacing: 3px;
	 padding: 1rem 0;
}
 
.header-nav.transparent .nav-link:hover, .header-nav.transparent .nav-link:focus {
	 background-color: rgba(0,0,0,0.3);
	 transition: all 0.3s ease-in;
}

.header-nav.transparent.sticky {
	 background-color: rgba(21,21,21,.95) !important;
	 transition: all 0.5s ease;
}

.header-nav.sticky a{
	transition: all 0.5s ease-in;
	padding-top:13px;
	padding-bottom: 13px;
}

.header-nav.transparent.sticky  .nav-link:hover,.header-nav.transparent.sticky  .nav-link:focus{
	background-color: rgba(255,255,255,1);
	color:black !important;
	transition: all 0.3s ease-in;
}


/*white navbar*/

.header-nav.light{
	background: white !important;
	transition: all 0.5s ease-in;
}

.header-nav.light .nav-link{
	color: black !important;
}

.header-nav.light.sticky {
	 background-color: white !important;
	 transition: all 0.5s ease;
}

.header-nav.light .nav-link:hover, .header-nav.light .nav-link:focus {
	 background-color: rgba(0,0,0,0.9) !important;
	 color: white !important;
	 transition: all 0.3s ease-in;
}

