}
.box-div .scroll-btn {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
}
.box-div .scroll-btn > *:hover, .box-div .scroll-btn > *:focus, .box-div .scroll-btn > *.active {
    color: #fff;
}
.box-div .scroll-btn > *:hover, .box-div .scroll-btn > *:focus, .box-div .scroll-btn > *:active, .box-div .scroll-btn > *.active {
    opacity: 0.8;
    filter: alpha(opacity=80);
}
.box-div .scroll-btn .mouse {
    position: relative;
    display: block;
    width: 35px;
    height: 55px;
    margin: 0 auto 20px;
    box-sizing: border-box;
    border: 3px solid white;
    border-radius: 23px;
}
.box-div .scroll-btn .mouse > * {
    position: absolute;
    display: block;
    top: 29%;
    left: 50%;
    width: 8px;
    height: 8px;
    margin: -4px 0 0 -4px;
    background: white;
    border-radius: 50%;
    -webkit-animation: ani-mouse 2.5s linear infinite;
    animation: ani-mouse 2.5s linear infinite;
}

.box-div .scroll-btn.dark .mouse{
   border: 3px solid #212121 !important;
}

.box-div .scroll-btn.dark .mouse > * {
   background: #212121 !important;
}


/* mouse animation */

@-webkit-keyframes ani-mouse {
    0% {
        opacity: 1;
        top: 29%;
   }
    15% {
        opacity: 1;
        top: 50%;
   }
    50% {
        opacity: 0;
        top: 50%;
   }
    100% {
        opacity: 0;
        top: 29%;
   }
}
@keyframes ani-mouse {
    0% {
        opacity: 1;
        top: 29%;
   }
    15% {
        opacity: 1;
        top: 50%;
   }
    50% {
        opacity: 0;
        top: 50%;
   }
    100% {
        opacity: 0;
        top: 29%;
   }
}
